@import "../node_modules/reset-css/sass/reset";

:root {
  --darkGray: #847e89;
  --xdarkGray: #43293e;
  --black: #262032;
}
* {
  box-sizing: border-box;
}

body {
  font-family: "Spartan", sans-serif;
  font-weight: 500;
  color: var(--xdarkGray);
  font-size: 12px;
  position: relative;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
}
h1 {
  font-weight: 700;
  font-size: 2rem;
  margin-bottom: 2rem;
  max-width: 25rem;
  line-height: 2.5rem;
}
h1,
h2 {
  font-weight: 700;
  color: var(--black);
}
h2 {
  text-transform: uppercase;
  font-size: 1rem;
  letter-spacing: 4px;
  margin-bottom: 1rem;
}
p {
  line-height: 1.5;
}

#navcontainer {
  height: 80px;
  background-color: white;
  top: -80px;
  left: 0;
  width: 100%;
  position: absolute;
  z-index: 150;
  display: flex;
  flex-direction: row;
  align-items: center;
  transition: transform 300ms ease-in-out;
  &.active {
    transform: translateY(80px);
  }
}

.container {
  width: 100%;
  overflow: hidden;
  display: grid;
  grid-template-columns: 100vw;
  grid-template-rows: 50vh 50vh 300px auto 300px;
}

span.button {
  position: absolute;
  top: 0;
  left: 0;
  width: 80px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100;
}
span.title {
  position: absolute;
  margin: 2rem auto;
  font-size: 20px;
  top: 0;
  left: 0;
  z-index: 50;
  color: white;
  display: block;
  width: 100%;
  text-align: center;
}

nav {
  width: 100%;
  ul {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: flex-end;
    margin: 0 1.5rem;
    li {
      padding: 0.5rem;
      margin: 0.5rem;
      position: relative;
      cursor: pointer;
      color: var(--xdarkGray);
      &:after {
        display: block;
        content: "";
        background-color: white;
        height: 2px;
        width: 100%;
        position: absolute;
        bottom: 4px;
        left: 0;
        transform: scale3d(0, 1, 1);
        transition: transform 300ms ease-in-out;
      }
      &:hover:after {
        transform: scale3d(1, 1, 1);
      }
    }
  }
  .close {
    margin-right: auto;
    margin-left: 0;
  }
}

#hero-image {
  position: relative;
  overflow: hidden;
}
.hero-slide {
  background-position: center center;
  background-size: cover;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: translateY(0);
  img,
  picture {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  &.fromTop {
    transform: translateY(-100%);
  }
  &.fromBottom {
    transform: translateY(100%);
  }
}
#main-copy {
  overflow: hidden;
  grid-column: 1 / 2;
  grid-row: auto;
  position: relative;
}
span.shop {
  display: block;
  margin-top: 2rem;
  text-transform: uppercase;
  letter-spacing: 2px;
  cursor: pointer;
  img {
    transition: margin 300ms ease-in-out;
    margin-left: 0.5rem;
  }
  &:hover {
    img {
      margin-left: 1rem;
    }
  }
}

.nav__arrows {
  z-index: 100;
  position: absolute;
  width: 200px;
  height: 50px;
  left: 0;
  bottom: 0;
  bottom: env(safe-area-inset-bottom);
  display: flex;
  flex-flow: row nowrap;
  .arrow {
    user-select: none;
    background-color: var(--black);
    cursor: pointer;
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
#leftarrow {
  img {
    transform: translate3d(0, 0, 0);
    transition: transform 300ms ease-in-out;
  }
  &:hover img {
    transform: translate3d(-5px, 0, 0);
  }
}
#rightarrow {
  img {
    transform: translate3d(0, 0, 0);
    transition: transform 300ms ease-in-out;
  }
  &:hover img {
    transform: translate3d(5px, 0, 0);
  }
}

.slide {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 2rem 50px 2rem;
  transform: translateX(0);
  &.fromLeft {
    transform: translateX(-100%);
  }
  &.fromRight {
    transform: translateX(100%);
  }
}
.animating {
  transition: transform 300ms ease-in-out;
}

#bottom__left-image {
  grid-row: auto;
  grid-column: 1 / 2;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

#bottom__copy {
  grid-row: auto;
  grid-column: 1 / 2;
  padding: 4rem 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#bottom__right-image {
  grid-row: auto;
  grid-column: 1/ 2;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
@media (min-width: 900px) {
  h1 {
    font-size: 2.5rem;
    line-height: 3rem;
    max-width: none;
  }
  #navcontainer {
    top: 2rem;
    left: 8rem;
    width: auto;
    background-color: unset;
    position: absolute;
  }
  nav {
    width: auto;
    padding: 0;
    ul {
      justify-content: initial;
      li {
        color: white;
      }
    }
  }
  .menubutton,
  .close {
    display: none;
  }

  span.title {
    height: 80px;
    margin: 0;
    left: 3rem;
    width: auto;
    top: 2rem;
    display: flex;
    align-items: center;
  }
  .container {
    height: 100vh;
    width: 100vw;
    display: grid;
    grid-template-columns: 1fr 1fr 200px 1fr;
    grid-template-rows: 21fr 13fr;
  }
  .nav__arrows {
    height: 100px;
  }
  .slide {
    padding: 0 4rem;
    padding-bottom: 100px;
  }
  #hero-image {
    grid-column: 1 / 3;
    position: relative;
    overflow: hidden;
  }
  #main-copy {
    grid-column: 3 / 5;
  }
  #bottom__left-image {
    grid-row: 2 / 3;
    grid-column: 1 / 2;
  }
  #bottom__copy {
    grid-row: 2 / 3;
    grid-column: 2 / 4;
    padding: 0 4rem;
  }
  #bottom__right-image {
    grid-row: 2 / 3;
    grid-column: 4 / 5;
  }
}
@media (min-width: 1400px) {
  h1 {
    font-size: 3rem;
    line-height: 3.5rem;
  }
}
